<template>
    <div class="login">
        <div class="select">
            <img src="static/elm/img/login.png" alt="">
            <div class="select-wrap">
                <div class="select-item" :class="{selected :select1}" @click="select('select1')">短信登陆</div>
                <div class="select-item" :class="{selected :select2}" @click="select('select2')">密码登陆</div>
            </div>
        </div>
        <form class="login-item" v-if="select1" autocomplete="off">
            <section class="phone">
                <input type="tel" autocomplete="off" maxlength="11" placeholder="手机号" v-model="phoneNumber" :class="{errorNumber :!rightNumber}"
                key="phone">
                <button :disabled="disabled" @click.prevent="message()">获取验证码</button>
            </section>
            <section class="phone">
                <input type="tel" autocomplete="off" maxlength="5" placeholder="验证码" v-model="phone" key="passphone">
            </section>
            <section class="phone">
                温馨提示：未注册饿了么帐号的手机号，登录时将自动注册，且代表您已同意
                <router-link to="#">《用户服务协议》</router-link>
            </section>
            <button class="submit" @click.prevent="mylogin()">登陆</button>
        </form>
        <form class="login-item" v-if="select2" autocomplete="off">
            <section class="phone">
                <input autocomplete="off" type="text" maxlength="11" placeholder="手机/邮箱/用户名" v-model="name" key="name">
            </section>
            <section class="phone">
                <input autocomplete="new-password" v-if="!showPassword" type="password" placeholder="密码"  v-model="passWord" key="password"
                >
                <input autocomplete="off" v-else type="text" placeholder="明码"  v-model="passWord" key="passseen">
                <mt-switch v-model="showPassword" class="word"></mt-switch>
            </section>
            <button class="submit" @click.prevent="mylogin()">登陆</button>
        </form>
        <index-footer></index-footer>      
    </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
const indexFooter = () => import('@/components/footer/index-footer')
export default {
    components: {
        indexFooter
    },
    data(){
        return {
            select1: true,
            select2:false,
            disabled: false,
            phoneNumber:null,
            phone: null,
            rightPhoneNumber:true,
            showPassword: true,
            name: undefined,
            passWord: null,
        }
    },
    computed: {
        ...mapState([
          'userInfo', 'logined'
        ]),
        //判断手机号码
        rightNumber: function (){
            if (this.phoneNumber) {
               return /^1\d*$/gi.test(this.phoneNumber); 
            }else {
                return true;
            };
        },
        rightPhone: function (){
            return /^1\d{10}$/gi.test(this.phoneNumber);
        },
    },
    methods: {
        ...mapMutations([
            'RECORD_USERINFO','sendLogin','accountLogin'
        ]),
        select(item) {
            if (item==='select1'){
                this.select1 = true;
                this.select2 = false;
            } else {
                this.select1 = false;
                this.select2 = true; 
            }
        },
        message() {
            alert ('验证码为：00000');
        },
        mylogin() {
            if (this.select1){
                if (this.rightPhone){
                    this.$store.commit('SET_LOGIN',
                    {phoneNumber: this.phoneNumber,phone: this.phone});
                    if (this.userInfo) {
                       this.$router.go(-1); 
                    }
                } else {
                    this.rightPhoneNumber = false;
                    alert ('请输入正确的手机号码！')
                }
            } else {

            }

        }
    }
}
</script>
<style scoped>
.login{
    width: 100vw;
    padding: 10vw;
}
.select{
    width: 80vw;
}
.select img{
    display: block;
    width: 40vw;
    margin:0 auto;
}
.select-wrap{
    width: 100%;
    font-size: 0;
    padding: 10vw 10vw 0 10vw;
}
.select-item{
    display: inline-block;
    width: 16vw;
    margin: 0 5vw;
    text-align: center;
    font-size: 4vw;
    font-weight: 600;
    height: 8vw;
    line-height: 8vw;
    color: rgb(59, 59, 59);
}
.selected{
    color: #2395ff;
    border-bottom: 2px solid #2395ff;
}
.login-item section{
    margin-top: 5vw;
    position: relative;
    width: 100%;
    color: rgb(185, 185, 185);
}

.phone input{
    width: 100%;
    height: 10vw;
    padding-left: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    color: #333;
    -webkit-appearance: none;
}
.phone button{
    position: absolute;
    top: 0;
    right: 10px;
    width: 20vw;
    height: 10vw;
    background-color: inherit;
    color: #ccc;
}
input:focus{
    border-color: #2395ff;
}
input.errorNumber{
    border-color: #f1a196;
}


.submit{
    width: 100%;
    height: 10vw;
    text-align: center;
    background-color: #4cd96f;
    border-radius: 4px;
    color: azure;
    font-size: 4vw;
    margin-top: 5vw;
}
.word{
    position: absolute;
    right: 0;
    top: 2vw;
    height: 6vw;
}

</style>







